.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
  display: inline-block;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #eee;
  border: 2px solid #6caddc;
}
.card > .img-container > img {
  width: 60%;
}



